import { CrossSellingPointer } from "../../others";
import { RCL } from ".";
import $ from "jquery";
/**
 * Add new category in Posts / Pages > Categories
 */

var crossRclAddCategory = function crossRclAddCategory() {
  return $("select#parent").length && // only for hierarchical taxonomies
  CrossSellingPointer.isActionAvailable(RCL, "add-category") && $("body.wp-admin.edit-tags-php:not(.woocommerce-page,.post-type-attachment) form#addtag #submit").one("click", function () {
    new CrossSellingPointer($(this), RCL, "add-category", "bottom");
  });
};

export { crossRclAddCategory };